</template>
<script>
export default {
    data(){
        return{
            isshow:false,
        }
    },
    methods:{
        show(){
            if(this.isshow){
                this.isshow=false;
            }else{
                 this.isshow=true;
            }
        },
         search(){
            this.$router.push({path:"/search"});
        },
        sort(){
            this.$router.push({path:"/sort"});
        },
        rank(){
           this.$router.push({path:"/rank"});
        },
         my(){
             if(this.$store.state.user.islogin==true){
                 this.$router.push({path:"/my"});
             }else{
                 this.$router.push({path:"/login"});
             }
           
        }
    }
}
