<template>
<!-- 首页漫画部分 -->
    <div>
        <h2>精品·企鹅娘打滚推荐</h2>
                <div class="recommend">
                    <ul>
                        <li v-for="(item,index) in $store.state.books.recommend" :key="index" @click="$store.commit('books/details',{item})">
                            <img v-bind:src="item.src" alt="">
                            <strong>{{item.name}}</strong>
                            <small>{{item.brief}}</small>
                        </li>
                    </ul>
                </div>
         <h2> 热门·OMG…盘它！</h2>
                <div class="hot">
                    <ul>
                        <li  v-for="(item,index) in $store.state.books.hot" :key="index" @click="$store.commit('books/details',{item})">
                            <img v-bind:src="item.src" alt="">
                            <strong>{{item.name}}</strong>
                            <small>{{item.sort}}</small>
                        </li>
                    </ul>
                </div>
         <h2>新作·叮~热敷敷出炉</h2>
                <div class="new">
                    <ul>
                        <li v-for="(item,index) in $store.state.books.new" :key="index" @click="$store.commit('books/details',{item})">
                            <img v-bind:src="item.src" alt="">
                            <strong>{{item.name}}</strong>
                            <small>{{item.sort}}</small>
                        </li>
                     
                    </ul>
                </div>
    </div>
</template>
<script>
export default {
     watch:{
      "$store.state.books.isdetails":function(newvalue){
            if(newvalue==true){
                this.$router.push({path:"/details"});
            }
        }
    },
    beforeDestroy(){
       this.$store.commit('books/jump_details');
    }
}
</script>
<style lang="scss" scoped>
@mixin content($width){
     ul{
            list-style:none;
            width:100%;
            li{
                width:$width;
                margin-left:4%;
                float:left;
                background-color:#f9f9f9;
                overflow:hidden;
                img{
                    display:block;
                    width:100%;
                    margin-bottom:0.5rem;
                }
                strong{
                    display: block;
                    margin-bottom: 0.2rem;
                    font-size: 0.75rem;
                    color: #333;
                    padding-top: 1px;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
                small{
                    display: block;
                    margin-bottom: 0.7rem;
                    font-size: 0.6rem;
                    color: #0c1220;
                    white-space: nowrap;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }
            }
        }
        ul::after{
            content:"";
            display:block;
            clear:both;
        }
}
    h2{
    line-height: 2.25rem;
    font-size: 0.8rem;
    color: #333;
    padding: 0 0.5rem;
    margin:0;
    }
    .recommend{
       @include content(44%);
    }
    .hot{
       @include content(28%);
    }
    .new{
       @include content(28%);
    }
</style>